lign-items: center;\n\n    h1 {\n      color: #ffff;\n      text-transform: uppercase;\n      font-size: 22px;\n      user-select: none;\n    }\n  }\n\n  .navbar {\n    position: absolute;\n    top: 50px;\n    left: -180px;\n    width: 150px;\n    transition: all 0.3s;\n    padding: 8px;\n    background-color: $color-primary;\n    overflow: hidden;\n\n    &.open {\n      left: 0;\n    }\n    // .nav-list{\n    //   display: inline-block;\n    // }\n\n    // .nav-item{\n    //   display: inline-block;\n    // }\n    a {\n      display: inline-block;\n      text-decoration: none;\n      color: white;\n      padding: 8px;\n      margin-bottom: 5px;\n      width: 100%;\n    }\n  }\n}\n\n.navbar .nav-list .nav-item a:hover:after {\n  transform: scaleX(0.5);\n  transition: 0.3s linear;\n}\n\n// Hero\n.hero {\n  display: flex;\n  align-items: center;\n  min-height: 380px;\n  width: 100%;\n  text-align: center;\n  background-position: center;\n  background-image: linear-gradient(rgba(0, 0, 5, 0.5), rgba(0, 0, 5, 0.5)), url(\"../public/images/heros/hero-image_2.jpg\");\n\n  .hero-inner {\n    margin: 0 auto;\n  }\n\n  .hero-title {\n    color: white;\n    font-weight: 600;\n    font-size: 36px;\n    text-shadow: 1px 1px #141414;\n  }\n\n  .hero-subtitle {\n    color: white;\n    margin-top: 16px;\n    font-size: 18px;\n    font-weight: 600;\n    text-shadow: 1px 1px #141414;\n  }\n}\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n\n  .content {\n    padding: 32px;\n  }\n\n  .find {\n    width: 100%;\n    margin-top: 30px;\n    margin-bottom: 50px;\n    text-align: center;\n\n    h1 {\n      font-weight: 700;\n      font-size: 2em;\n      margin-bottom: 50px;\n    }\n  }\n\n  .restaurant {\n    margin: 32px auto auto;\n    text-align: left;\n    display: grid;\n    grid-row-gap: 16px;\n  }\n\n  .list_restaurant {\n    width: 100%;\n    border-radius: 5px;\n    overflow: hidden;\n    position: relative;\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n  }\n\n  .kota_restaurant {\n    position: absolute;\n    top: 8px;\n    left: 16px;\n    border-radius: 5px;\n    padding: 5px 10px 5px;\n    font-weight: 500;\n    background-color: $color-secondary;\n    color: white;\n  }\n\n  .content_restaurant {\n    padding: 16px;\n  }\n\n  .thumbnail_restaurant {\n    width: 100%;\n    max-height: 200px;\n  }\n\n  .rating_restaurant {\n    font-size: 16px;\n    text-transform: uppercase;\n    color: #999;\n    font-weight: 500;\n\n    .rating_value_restaurant {\n      color: red;\n      text-decoration: none;\n      display: inline-block;\n      min-width: 44px;\n      min-height: 44px;\n    }\n  }\n\n  .title_restaurant {\n    font-weight: 700;\n    font-size: 24px;\n    transition: 0.3s;\n\n    a {\n      text-decoration: none;\n      color: inherit;\n      display: inline-block;\n      min-width: 44px;\n      min-height: 44px;\n    }\n\n    &:hover {\n      color: $color-secondary;\n    }\n  }\n\n  .deskripsi_restaurant {\n    margin-top: -50px;\n    font-size: 12px;\n    line-height: 1.5em;\n  }\n}\n\n// Footer\nfooter {\n  background-color: $color-primary;\n  color: white;\n  font-size: 14px;\n  padding: 2em;\n  width: 100%;\n  text-align: center;\n}\n"],"sourceRoot":""}]);
